.about-page {
   
        margin-top: 40px;
        
        h4 {
            margin-top: 20px;
            font-size: 20px;
            
        }
        
       
        h3 {
            margin-top: 30px;
            font-size: 32px;
        }
        .us-container {
            width: 100%;
            justify-content: space-between;
            img {
                min-width: 150px;
                max-height: 240px;
                border-radius: 10px;
            }
            
            h4 {
                font-size: 20px;
                font-weight: bold;
            }
            // .a-fenell {}
            .about-us {
                display: flex;
                flex-direction: column;
                align-items: center;
                p {
                    
                    margin-top: 20px;
                    padding-right: 20px;
                    padding-left: 20px;
            }
        }
        // .a-baron{}
    }
}